import React from 'react';
import Cookies from 'universal-cookie';
let MoneyButton = require('@moneybutton/react-money-button').default

export default class LoginAlert extends React.Component{
    constructor(props){
        super(props);
        this.state={
            input:''
        }
    }

    onPaymentHandler=(payment)=>{
        
        if(this.state.input!=''){
            localStorage.setItem('userId', payment.userId.toString(), { path: '/' });
            localStorage.setItem('username', this.state.input, { path: '/' });
            this.props.loginHandler();
        }
        
        console.log(payment.userId)
        console.log(payment);
    }

    inputOnChangeHandler=(event)=>{
        this.setState({
            input:event.target.value
        })
    }


    render(){
      console.log(this.props.changingUsername);
      var paymentAmount;
      this.props.changingUsername?paymentAmount='1':paymentAmount='0.01'

      return (
        <div className="login-container">
            <div className="login-alert w-form">
              <form id="email-form-3" name="email-form-3" data-name="Email Form 3" className="login-form">
                <div className="div-block-2" onClick={this.props.xHandler}>
                  <div className="login-cancel" >X</div>
                </div>
                <div className="login-title" style={{pointerEvents:'none'}} >Login</div>
                    <input 
                        onChange={this.inputOnChangeHandler}
                        type="text" 
                        className="login-username w-input" 
                        maxLength={256} name="name-2" 
                        data-name="Name 2" 
                        placeholder="Desired Username" id="name-2" />
                <div className="mb-container" ><MoneyButton
                  to='112'
                  amount={paymentAmount}
                  currency='USD'
                  onPayment={this.onPaymentHandler}
                /></div>
              </form>

            </div>
          </div>
    );}
}